.dialog {
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  position: fixed;
  display: none;
  background: rgba(0, 0, 0, 0.5);
  &.active{
    display: flex;
  }
}
.dialogContent {
  margin: auto;
  background: white;
  border-radius: 5px;
  min-height: 50px;
  min-width: 300px;
  padding: 15px;
}