.input {
  padding: 10px;
  width: 100%;
  height: 34px;
  font-size: 16px;
  border: 1px solid #ced4da;
  border-radius: 2px;
  &:focus {
    color: #495057;
    background-color: #fff;
    border-color: #80bdff;
    outline: 0;
    box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
  }
}